.psol-comp-QRScan-container {
  position: relative;
  height: 100%;
  width: 100%;
}

.psol-comp-QRScan-video-container {
  position: relative;
  height: 100%;
  width: 100%;
}

.psol-comp-QRScan-canvas {
  display: none;
}

.psol-comp-QRScan-cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.5;
  border: 1px solid black;
}

.psol-comp-QRScan-focusQuadrat {
  position: absolute;
  border: 4px dashed #ffa500;
  pointer-events: none;
}
