.psol-comp-dialogbase-backdrop {
  position: fixed;
  display: none;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0.5;
  background: #000000;
  z-index: 99999;
  pointer-events: none;
}

.psol-comp-dialogbase-container {
  display: none;
  position: fixed;
  z-index: 100000;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.psol-comp-dialogbase-showModal {
  overflow: hidden;
}

.psol-comp-dialogbase-dialog {
  /*position: absolute;*/
  display: flex;
  background-color: #ffffff;
  border-radius: 20px;
  flex-direction: column;
}

.psol-comp-dialogbase-dialog[data-b-view='psol-comp-ProgressDialog'] {
  z-index: 100001;
}

.psol-comp-dialogbase-dialog-container {
  flex-grow: 1;
  min-height: 0;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.psol-comp-dialogbase-dialog-container-padding {
  position: relative;
  padding: 25px;
  flex-grow: 1;
  min-height: 0;
  overflow-y: auto;
}

.psol-comp-dialogbase-dialog-auto-height {
  width: 90%;
  max-height: 90%;
  height: auto;
}

.psol-comp-dialogbase-dialog-fixed-height {
  width: 90%;
  height: 90%;
}

@media only screen and (min-width: 768px) {
  .psol-comp-dialogbase-dialog-auto-height {
    width: 60%;
    max-height: 60%;
    height: auto;
  }
  .psol-comp-dialogbase-dialog-fixed-height {
    width: 60%;
    height: 60%;
  }
}
@media only screen and (min-width: 1024px) {
  .psol-comp-dialogbase-dialog-auto-height {
    width: 45%;
    max-height: 60%;
    height: auto;
  }
  .psol-comp-dialogbase-dialog-fixed-height {
    width: 45%;
    height: 60%;
  }
}
@media only screen and (min-width: 1200px) {
  .psol-comp-dialogbase-dialog-auto-height {
    width: 40%;
    max-height: 60%;
    height: auto;
  }
  .psol-comp-dialogbase-dialog-fixed-height {
    width: 40%;
    height: 60%;
  }
}
@media only screen and (min-width: 1600px) {
  .psol-comp-dialogbase-dialog-auto-height {
    width: 30%;
    max-height: 60%;
    height: auto;
  }
  .psol-comp-dialogbase-dialog-fixed-height {
    width: 30%;
    height: 60%;
  }
}

@media (max-height: 775px) and (min-width: 768px) {
  .psol-comp-dialogbase-dialog-auto-height {
    width: 80%;
    max-height: 90%;
    height: auto;
  }
  .psol-comp-dialogbase-dialog-fixed-height {
    width: 70%;
    height: 65%;
  }
}

.psol-comp-dialogbase-dialog-header {
  border-bottom: 2px solid #dddddd;
  padding-bottom: 10px;
  flex-shrink: 0;
}

.psol-comp-dialogbase-dialog-header-title {
  font-weight: bold;
  color: #9ab6ca;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: calc(100% - 44px);
  line-height: 48px;
}

.psol-comp-dialogbase-dialog-header-closebtn {
  width: 44px;
  height: 44px;
  background-position: 50%, 50%;
  background-repeat: no-repeat;
  background-size: 32px;
  background-image: url(../img/dialogbase/close_button.png);
  cursor: pointer;
}

.psol-comp-dialogbase-dialog-content {
  flex-grow: 1;
  overflow-y: auto;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  -webkit-overflow-scrolling: touch;
}

.psol-comp-dialogbase-dialog-footer {
  flex-shrink: 0;
}

.psol-comp-dialogbase-dialog-footer div:last-child {
  margin-right: 0;
}

.psol-comp-dialogbase-buttoncontainer {
  display: flex;
}

.psol-comp-dialogbase-dialog-container-fullwidth {
  width: calc(100% - 16px);
  margin-left: 8px;
  margin-right: 8px;
}

.psol-comp-dialogbase-dialog-container-fullheight {
  height: calc(100% - 16px);
  margin-top: 8px;
  margin-bottom: 8px;
}

.psol-comp-dialogbase-button {
  margin-right: 10px;
  margin-top: 10px;
}

.psol-comp-dialogbase-hidden {
  visibility: hidden;
}
