.psol-comp-drawing-container {
  position: relative;
  width: 100%;
  height: 100%;
}
.psol-comp-drawing-overlay {
  display: none;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ffffff;
  opacity: 0.4;
  position: absolute;
}

.psol-comp-drawing-canvas-container,
.psol-comp-drawing-canvas,
.psol-comp-drawing-canvas-grid {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.psol-comp-drawing-canvas-grid-container {
  pointer-events: none;
}

.psol-comp-drawing-controls-container {
  position: absolute;
  bottom: 0;
}

.psol-comp-drawing-sticky-arrow {
  position: absolute;
  display: none;
}

.psol-comp-drawing-size-container,
.psol-comp-drawing-color-container {
  position: absolute;
  display: none;
  min-width: 150px;
  max-width: 250px;
  text-align: center;
  overflow: hidden;
}

.psol-comp-drawing-controls-title {
  font-weight: bold;
  padding-bottom: 5px;
}

.psol-comp-drawing-context-menu {
  display: none;
  position: absolute;
  height: 44px;
  width: 44px;
  transform: translate(-50%, -50%);
}
