.psol-comp-cadformats-formats-scrollable {
  overflow: auto;
  height: 100%;
  padding: 10px;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
}

.psol-comp-cadformats-filter {
  margin-bottom: 5px;
  flex: 0 0 auto;
}

.psol-comp-cadformats-filter-input {
  width: 50%;
  margin-right: 10px;
  font-weight: normal;
  padding: 3px;
  color: #000000;
  background: #ffffff;
  box-sizing: border-box;
  cursor: text;
  display: inline-block;
  text-align: start;
  letter-spacing: normal;
  word-spacing: normal;
  text-transform: none;
  text-indent: 0;
  text-rendering: auto;
}

.psol-comp-cadformats-icon-cadFormats {
  line-height: 16px;
  height: 16px;
  width: 16px;
  margin: 0.25em;
  vertical-align: middle;
  display: inline-block;
  padding-right: 5px;
}

.psol-comp-cadformats-container {
  max-height: calc(100% - 10px);
  width: 100%;
  position: relative;
}

.psol-comp-cadformats-filter-2d,
.psol-comp-cadformats-filter-3d,
.psol-comp-cadformats-filter-check-box {
  margin-left: 5px;
}

.psol-comp-cadformats-checkbox {
  background-color: #ffffff;
  background-image: none;
  text-align: center;
  line-height: 16px;
  width: 16px;
  height: 16px;
  border-radius: 2px;
  border: 1px solid #a8a8a8;
}

.psol-components-cadformats-checkfilter {
  height: 12px;
  width: 12px;
  fill: #999999;
  border: 1px solid #999999;
  background: url(../img/viewbase/checkbox_checked.png);
  background-repeat: no-repeat;
  background-size: 12px;
  border-radius: 2px;
}

.psol-comp-cadformats-message-warning {
  display: none;
  bottom: 0;
  color: #ff0000;
  padding: 10px;
  width: 100%;
}

.psol-comp-cadformats-list {
  cursor: pointer;
}
