/*********************************  text-ellipsis HACK ON ********************************/
div.psol-comp-index-item-desc {
  font-size: 14px;
  margin: 5px;
  word-wrap: break-word;
  overflow: hidden;
  text-overflow: ellipsis;
}

div.psol-comp-index-item-desc-line-clamp {
  /* autoprefixer: off */
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

/* MS Edge targeting */
@supports (-ms-ime-align:auto) {
  div.psol-comp-index-item-desc-line-clamp::before {
    content: '\002026';
    width: 15%;
    right: 0;
    top: 1.3em;
    position: absolute;
    text-align: right;
    background: linear-gradient(90deg, transparent 0%, white 50%);
    padding-right: 5%;
  }

  div.psol-comp-index-item-desc-line-clamp::after {
    content: '';
    height: 100%;
    width: 100%;
    background-color: inherit;
    position: absolute;
  }

  div.psol-comp-index-item-desc-line-clamp {
    max-height: 2.6em;
  }

  div.psol-comp-index-item-desc {
    font-size: 14px;
    margin: 5px;
    background-color: #ffffff;
    display: block;
    line-height: 1.3em;
    overflow: hidden;
    position: relative;
    text-overflow: ellipsis;
    white-space: normal;
    word-wrap: break-word;
  }
}

/* Firefox/Gecko browser targeting */
@supports not (-webkit-line-clamp: 2) {
  div.psol-comp-index-item-desc-line-clamp::before {
    content: '\002026';
    width: 15%;
    right: 0;
    top: 1.3em;
    position: absolute;
    text-align: right;
    background: linear-gradient(90deg, transparent 0%, white 50%);
    padding-right: 5%;
