  }

  div.psol-comp-index-item-desc {
    font-size: 14px;
    margin: 5px;
    background-color: #ffffff;
    display: block;
    line-height: 1.3em;
    overflow: hidden;
    position: relative;
    text-overflow: ellipsis;
    white-space: normal;
    word-wrap: break-word;
  }
}

