.psol-comp-suggestionsTags-container {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
}

.psol-comp-suggestionsTags-item {
  display: flex;
  align-items: center;
  margin: 5px;
  border-radius: 4px;
  color: rgba(0, 0, 0, 0.6);
  background: rgba(0, 0, 0, 0.08);
  flex: 0 0 auto;
  cursor: pointer;
}

.psol-comp-suggestionsTags-item-label {
  padding-right: 0;
  padding-left: 5px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.psol-comp-suggestionsTags-item-remove {
  padding-right: 5px;
  padding-left: 5px;
  display: inline-block;
  color: rgba(0, 0, 0, 0.4);
  text-decoration: none;
}

.psol-comp-similarWordsTag-container,
.psol-comp-didYouMeanTags-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 5px;
}

.psol-comp-similarWordsTag-container > span,
.psol-comp-didYouMeanTags-container > span {
  font-weight: bold;
}
