.psol-comp-dropdown-container {
  width: 100%;
  font-size: 14px;
}

.psol-comp-dropdown-list {
  display: block;
  overflow: hidden;
  padding: 2px 15px;
}

.psol-comp-dropdown-list:hover {
  color: #ffffff;
  background: #4d90fe;
  text-decoration: none;
  cursor: pointer;
}

.psol-comp-dropdown-list-unclickable:hover {
  cursor: default;
  background: none;
  color: inherit;
}

.psol-comp-dropdown-filter {
  border-top: 0;
  border-bottom: 0;
}

.psol-comp-dropdown-filter-input {
  width: calc(100% - 10px);
  margin: 5px;
  border-radius: 0;
  padding: 6px;
  box-sizing: border-box;
  border-color: transparent;
  outline: 1px solid #888888;
  font: inherit;
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
  text-overflow: ellipsis;
}

.psol-comp-dropdown-dropdown-container {
  display: inline-block;
  background-color: white;
  position: relative;
  color: black;
}

.psol-comp-dropdown-dropdown {
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.16), 0 2px 5px 0 rgba(0, 0, 0, 0.26);
  z-index: 99;
  position: absolute;
  outline: 1px solid #888888;
  background-color: #ffffff;
  overflow-x: hidden;
  overflow-y: auto;
  max-height: 200px;
}

.psol-comp-dropdown-dropdown-bottom {
  top: 100%;
}

.psol-comp-dropdown-dropdown-top {
  bottom: 100%;
}

.psol-comp-dropdown-dropdown-content {
  border-top: 0;
  max-height: 200px;
  overflow-y: auto;
  overflow-x: hidden;
}

.psol-comp-dropdown-selected {
  position: relative;
  background-color: white;
  border-radius: 0;
  border-width: 2px;
  border-style: solid;
  border-color: transparent;
  background-image: url(../img/viewbase/arrow_down.png);
  background-position: right 8px center;
  background-repeat: no-repeat;
  background-size: 16px;
  padding-right: 32px;
  padding-left: 5px;
  padding-top: 6px;
  padding-bottom: 6px;
  outline: 1px solid #888888;
  font: inherit;
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
  max-width: 100%;
  text-overflow: ellipsis;
  cursor: pointer;
}

.psol-comp-dropdown-selected-multiSelection {
  padding-bottom: 2px;
  padding-top: 2px;
}

.psol-comp-dropdown-filter-input:focus {
  outline: 2px solid #4d90fe;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(77, 144, 254, 0.6);
}

.psol-comp-dropdown-img {
  border: 1px solid #d3d3d3;
  width: 17px;
  height: 17px;
  vertical-align: middle;
  margin-right: 5px;
  flex-shrink: 0;
}

.psol-comp-dropdown-checkbox {
  background-color: #ffffff;
  background-image: none;
  text-align: center;
  line-height: 16px;
  width: 16px;
  height: 16px;
  border-radius: 2px;
  border: 1px solid #a8a8a8;
}

.psol-comp-dropdown-multiselected-option {
  background-color: #9ab6ca;
  color: white;
  padding: 3px 6px;
  border-radius: 3px;
  display: inline-block;
  margin-bottom: 2px;
  margin-right: 3px;
}

.psol-comp-dropdown-visibleValue {
  width: 100%;
  white-space: nowrap;
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
}

.psol-comp-dropdown-multiselected-option > .psol-comp-viewbase-svgicon {
  width: 14px;
  height: 14px;
  margin-right: 4px;
}

.psol-comp-dropdown-selected-multiselected-inline {
  display: inline-flex;
  width: 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.psol-comp-dropdown-selected[data-nolabel='true'] .psol-comp-dropdown-visibleValue {
  display: none;
}
